.popup,
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  // display: flex;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 100000;
  overflow: hidden;
  background-color: $modalBgColor;

  &__wrap,
  &__dialog {
    max-width: rem(500);
    max-height: 80vh;
    overflow-y: auto;
  }
  &__content {
    position: relative;
    background-color: $white;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 4px;
  }
  &__close {
    position: absolute;
    top: rem(10);
    right: rem(14);
    // font-size: rem(30);
    color: $black;
    opacity: 0.5;
    // font-weight: 700;
  }
  // &__form,
  &__header,
  &__body {
    padding: rem(15);
    border-bottom: 1px solid #dee2e6;
  }
  &__title {
    font-weight: 500;
    font-size: rem(20);
  }
  &__footer {
    padding: rem(15);
  }
}
@media (max-width: em($mediaSm)) {
  .modal {
    &__dialog {
      @include SizeRem("max-width", 500, 280, 768, 320);
    }
    &__wrap {
      @include SizeRem("max-width", 500, 280, 768, 320);
    }
  }
}
