.dropdown {
  display: inline-block;
  position: relative;
  transition: all 2s;
  // &__toggle {
  // width: rem(15);
  // height: rem(15);
  // border: none;
  background-color: #00ffff;
  // }
  &__toggle {
    img {
      width: 100%;
    }
    // svg {
    //   fill: #00ffff;
    // }
  }
  &__menu {
    width: 100%;
    padding: 0 0 rem(15) rem(10);
    // display: flex;
    flex-direction: column;
    // border: 1px solid $primary;
    // border-radius: 2px;
    position: absolute;
    top: 100%;
    left: 0;
    display: none;
    z-index: 1000;
  }
  &__item {
    margin-top: rem(15);
  }
  &__link {
    text-decoration: none;
    color: $primary;
    &:hover {
      color: $success;
    }
  }
}
