.search {
  max-width: $search;
  height: $search;
  padding-left: $search;
  position: relative;
  border: $searchBorder;
  border-radius: $search;
  background-color: $white;
  transition: $searchTransition;
  box-shadow: 0 0 0 5px #2573af;
  overflow: hidden;
  &__icon {
    width: $search;
    height: 100%;
    border-radius: 50%;
    background-color: $white;
  }
  &__clear {
    // display: none;
    width: $search;
    height: 100%;
    border-radius: 50%;
    background-color: $white;
  }
  &__input {
    min-width: rem(100);
    height: 100%;
    display: flex;
    position: relative;
    border-radius: $search;
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      font-size: $searchFZ;
      padding: rem(10) 0;
    }
  }
}
.active {
  // width: rem(460);
  // width: 90%;
  max-width: 100%;
}
