.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 100000;
  overflow: hidden;
  background-color: $modalBgColor;
  // overflow-y: auto;

  &__wrap {
    max-width: rem(400);
    max-height: 100vh;
    overflow-y: auto;
  }
  &__content {
    padding: rem(50);
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: $myColorDarkBlue;
    font-size: rem(20);
    font-weight: 500;
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    max-width: rem(180);
    img {
      width: 100%;
    }
  }
  &__close {
    position: absolute;
    top: rem(10);
    right: rem(14);
    // font-size: rem(30);
    // width: rem(20);
    // height: rem(20);
    color: $white;
    font-weight: 700;
    line-height: 50%;
  }
  nav {
    margin-top: rem(87);
  }
  .divider {
    width: 80%;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    list-style: none;
    height: rem(290);
  }
  &__phone {
    margin: rem(40) 0 rem(130);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: rem(190);
    img {
      margin-right: rem(11);
    }
  }
  &__footer {
    max-width: rem(260);
    height: rem(48);
    display: flex;
    justify-content: space-between;
    flex: 1;
  }
}
@media (max-width: em($mediaSm)) {
  .mobile-menu {
    &__wrap {
      @include SizeRem("max-width", 400, 300, 768, 320);
    }
    &__content {
      padding: rem(40) rem(20);
    }
    &__logo {
      @include SizeRem("max-width", 180, 130, 768, 320);
      max-width: rem(130);
    }
    &__footer {
      @include SizeRem("max-width", 260, 220, 768, 320);
      img {
        @include SizeRem("height", 48, 40, 768, 320);
      }
    }
  }
}
