.carousel {
  display: block;
  margin: 0 auto;
  width: rem(900);
  position: relative;
  &__indicators {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    display: flex;
    justify-content: center;
    margin-right: 15%;
    margin-left: 15%;
    list-style: none;
    li {
      box-sizing: content-box;
      flex: 0 1 auto;
      width: rem(30);
      height: rem(6);
      margin-right: rem(3);
      margin-left: rem(3);
      cursor: pointer;
      background-color: $white;
      background-clip: padding-box;
      border-top: rem(10) solid transparent;
      border-bottom: rem(10) solid transparent;
      opacity: 0.5;
      transition: opacity 0.6s ease;
    }
    .active {
      opacity: 1;
    }
  }
  &__inner {
    width: 100%;
    height: rem(500);
    position: relative;
    overflow: hidden;
    // transition: all 0.5s;
  }
  &__slides {
    height: 100%;
    display: flex;
    transition: all 0.5s;
  }
  &__item {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__prev,
  &__next {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15%;
    color: $white;
    opacity: 0.5;
    text-align: center;
    font-size: rem(40);
    transition: opacity 0.15s ease;
    text-decoration: none;
    &:hover {
      opacity: 0.9;
      background-color: rgba(0, 0, 0, 0.2);
    }
  }
  &__prev {
    left: 0;
  }
  &__next {
    right: 0;
  }
}
