.btn {
  display: inline-block;
  border: none;
  border-radius: rem(4);
  background-color: transparent;
  font-size: rem(14);
  // font-weight: $myFontWeightBtn;
  color: $dark;
  text-align: center;
  line-height: 1.5;
  vertical-align: middle;
  transition: all 0.2s;
  cursor: pointer;
  // &:hover {
  //   background-color: $myBgColorBtn;
  //   color: $myColorBtn;
  // }
  &__close {
    width: rem(20);
    height: rem(20);
    font-size: rem(30);
    font-weight: 700;
    line-height: 50%;
    text-align: center;
    // color: $dark;
    // border: none;
    overflow: hidden;
    // &:hover {
    //   color: $primary;
    // }
  }
  &__text {
    padding: rem(5) 0;
    font-size: rem(18);
    // color: $dark;
    // border: none;
    &:hover {
      color: $primary;
    }
  }
  &__icon {
    width: rem(20);
    height: rem(20);
    font-size: rem(40);
    // font-weight: 700;
    line-height: 50%;
    text-align: center;
    // color: $dark;
    // border: none;
    overflow: hidden;
    &:hover {
      color: $primary;
    }
    // &-active {
    // }
  }
  &__submit {
    background-color: $myColorYellow;
    // color: $black;
    font-size: rem(16);
  }
  &__search {
    // position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    // right: 0;
    z-index: 10;
    &::before {
      content: "";
      width: rem(12);
      height: rem(12);
      position: absolute;
      border: 3px solid #287dfc;
      border-radius: 50%;
      transform: translate(rem(-2), rem(-2));
    }
    &::after {
      content: "";
      width: rem(3);
      height: rem(8);
      position: absolute;
      background-color: #287dfc;
      // border: 1px solid  #287dfc;
      border-radius: 5px;
      transform: translate(rem(5), rem(6)) rotate(-45deg);
    }
  }
  &__clear {
    display: flex;
    justify-content: center;
    align-items: center;
    &::before {
      content: "";
      width: rem(3);
      height: rem(15);
      position: absolute;
      background-color: #287dfc;
      // border: 1px solid  #287dfc;
      border-radius: 5px;
      transform: rotate(45deg);
      // content: "";
      // width: rem(12);
      // height: rem(12);
      // position: absolute;
      // border: 3px solid #287dfc;
      // border-radius: 50%;
      // transform: translate(rem(-2), rem(-2));
    }
    &::after {
      content: "";
      width: rem(3);
      height: rem(15);
      position: absolute;
      background-color: #287dfc;
      // border: 1px solid  #287dfc;
      border-radius: 5px;
      transform: rotate(-45deg);
      // translate(rem(5), rem(6))
    }
  }
  &__autoflash {
    position: relative;
    overflow: hidden;
    &::after {
      content: "";
      background-color: $white;
      height: 100%;
      // width: rem(30);
      width: 20%;
      display: block;
      position: absolute;
      top: 0;
      left: -30%;
      // -webkit-transform: skewX(-45deg) translateX(0);
      transform: skewX(-45deg) translateX(0);
      // -webkit-transition: none;
      transition: none;
      // animation: flash 3s ease-in-out infinite;
      animation: flash 3s ease-in-out infinite;
    }
  }

  @import "./button__btn";
}
