.burger {
  display: none;
  width: rem(36);
  height: rem(23);
  position: relative;
  cursor: pointer;
  transform: all 0.3s;
  z-index: 110000;
  // visibility: hidden;
  [data-active="false"] {
    ::after,
    ::before {
      content: "";
      display: inline-block;
      position: absolute;
      width: 100%;
      height: rem(3);
      background-color: $white;
    }
    ::before {
      top: 0;
    }
    ::after {
      bottom: 0;
    }
    [data-active="true"] {
      ::before {
        transform: rotate(45deg);
      }
      ::after {
        transform: rotate(-45deg);
      }
    }
  }
  &__icon {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: flex-end;
  }
  &__line {
    display: inline-block;
    position: absolute;
    width: 100%;
    height: rem(3);
    top: 50%;
    transform: translateY(-50%);
    background-color: $white;
    &-short {
      width: 80%;
    }
  }
}
