.card {
  max-width: rem(300);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.1);

  &__header {
    // height: rem(250);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  &__img {
    width: 100%;
    // height: 100%;
  }
  &__body {
    padding: rem(20);
  }
  &__title {
    font-size: rem(20);
    margin-bottom: rem(10);
  }
  &__text {
    margin-bottom: rem(10);
  }
  a {
    text-decoration: none;
  }
}
