.header {
  height: 55px;
  // color: $myColorDarkBlue;
  background-color: $myColorDarkBlue;
  display: flex;
  align-items: center;
  // justify-content: space-between;
  &__logo {
    width: 269px;
    img {
      width: 100%;
    }
  }
  &__menu {
    margin-left: 130px;
    max-width: 454px;
  }
  &__phone {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 157px;
    width: 159px;
    img {
      margin-right: 11px;
      width: 24px;
      height: 24px;
    }
  }
  .header__midl {
    padding: rem(10) rem(40);
    min-height: rem(60);
    background-color: $primary;
  }
  &__search {
    width: rem(500);
  }
}
@media (max-width: 1200px) {
  .header {
    &__nav {
      margin-left: 50px;
      width: 400px;
    }
    &__phone {
      margin-left: 82px;
    }
  }
}
@media (max-width: 992px) {
  .header {
    &__top {
      justify-content: space-between;
    }
    &__nav,
    &__phone {
      display: none;
    }
  }
}
@media (max-width: 768px) {
  .header {
    &__logo {
      width: 190px;
    }
  }
}
@media (max-width: 640px) {
}
@media (max-width: 480px) {
  .header {
    &__logo {
      width: 160px;
    }
  }
}
