.checkbox {
  position: relative;
  display: flex;
  align-items: center;
  &__real {
    width: rem(16);
    height: rem(16);
    position: absolute;
    opacity: 0;
    z-index: -1;
  }
  &__custom {
    display: inline-block;
    width: rem(16);
    height: rem(16);
    margin-right: 8px;
    position: relative;
    border: 2px solid $myColorYellow;
    border-radius: 3px;
    transition: 0.2s ease-in;
    cursor: pointer;
    &:hover {
      box-shadow: 0 0 0 1px $myColorYellow,
        0 0 10px $myColorYellow;
    }
    &::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 10px;
      // top: 0px;
      // left: 7px;
      top: 3px;
      left: 4px;
      border-radius: 5px;
      rotate: 45deg;
      transform: scale(0);
      background-color: $myColorBlue;
      transition: 0.2s ease-in;
    }
    //   &::before {
    //     content: "";
    //     position: absolute;
    //     width: 2px;
    //     height: 5px;
    //     // top: 4px;
    //     // left: 2px;
    //     top: 6px;
    //     left: 4px;
    //     border-radius: 5px;
    //     rotate: -45deg;
    //     transform: scale(0);
    //     background-color: $myColorBlue;
    //     transition: 0.2s ease-in;
    //   }
  }
  &__real + &__custom {
    // display: inline-block;
    // width: rem(16);
    // height: rem(16);
    // margin-right: 8px;
    // position: relative;
    // border: 2px solid $myColorYellow;
    // border-radius: 3px;
    // transition: 0.2s ease-in;
    // cursor: pointer;
    &:hover {
      box-shadow: 0 0 0 1px $myColorYellow,
        0 0 10px $myColorYellow;
    }
    &::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 10px;
      // top: 0px;
      // left: 7px;
      top: 3px;
      left: 4px;
      border-radius: 5px;
      rotate: 45deg;
      transform: scale(0);
      background-color: $myColorBlue;
      transition: 0.2s ease-in;
    }
    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 5px;
      // top: 4px;
      // left: 2px;
      top: 6px;
      left: 4px;
      border-radius: 5px;
      rotate: -45deg;
      transform: scale(0);
      background-color: $myColorBlue;
      transition: 0.2s ease-in;
    }
  }
  &__real:checked + &__custom {
    background-color: $myColorYellow;
    &::after {
      top: 1px;
      left: 7px;
      transform: scale(1);
    }
    &::before {
      top: 5px;
      left: 2px;
      transform: scale(1);
    }
  }
}
