.form {
  max-width: 480px;
  padding: rem(30);
  input[name="name"],
  input[name="email"],
  input[name="phone"],
  textarea {
    margin-top: 25px;
    // margin-bottom: 5px;
    padding: rem(18) rem(16);
    width: 100%;
    height: rem(60);
    font-size: rem(16);
    font-weight: rem(24);
    outline: none;
    resize: none;
    border: none;
    border-bottom: 1px solid $dark;
  }
  input::placeholder,
  textarea::placeholder {
    color: #808080;
  }
  &__button {
    margin-top: 25px;
    width: 100%;
    height: 80px;
    font-weight: 500;
    font-size: 24px;
    line-height: 24px;
  }
  &__policy {
    display: flex;
    position: relative;
    align-items: center;
    margin-top: 10px;
    input {
      width: rem(16);
      height: rem(16);
      // margin-top: 0px;
      margin-right: 8px;
      // background-color: black;
      // color: blueviolet;
    }
  }
  // &__checkbox {
  //   width: rem(15);
  //   height: rem(15);
  // }
  // input[type="checkbox"] {
  //   // width: rem(15);
  //   // height: rem(15);
  //   margin-top: 0px;
  //   margin-right: 8px;
  //   width: 15px;
  //   height: 15px;
  // }
}
