.accordion {
  max-width: 700px;
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    min-height: 50px;
    padding: 0 20px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    font-size: 18px;
    color: $dark;
    // cursor: pointer;
    &-active {
      color: $primary;
    }
  }
  &__content {
    max-height: 0px;
    // opacity: 0;
    overflow: hidden;
    transition: opacity 0.5s, max-height 0.6s, padding 0.1s;
    background-color: #fff;
    border: 1px solid rgba(224, 211, 211, 1);
    border-top: none;
    border-radius: 0 0 4px 4px;
    &-active {
      // padding: 20px 0 20px 0;
      // overflow: visible;
      opacity: 1;
    }
  }
  &__inner {
    display: block;
    padding: 20px;
  }
}
